h1{
  font-size: 4rem;
  color: #fff;
}

.App {
  text-align: center;
  position: relative;
  z-index: 1;
}

.App::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('./Img/pattern_interno.webp') no-repeat center center/cover;
  opacity: 0.7;
  z-index: -1; 
}

.welcome-message {
  font-size: 2rem;
  color: white;
  margin-top: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  padding-bottom: 1rem;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
