.sidebar {
  background-color: rgba(34, 44, 51, 1.0);
  padding: 20px;
  color: white;
  border: 1px solid #ccc;
}

.flex {
  align-items: center;
}

.logo {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.9);
  margin: 10px;
}

.nombre {
  font-size: 2.5rem;
  font-weight: bold;
  display: inline-block;
  vertical-align: middle;
}

.hidden {
  display: none;
}

.slidedown {
  animation: slidedown 0.4s forwards;
}

.slideup {
  animation: slideup 0.4s forwards;
}

.sidebar-lat-button {
  font-size: 0.8rem!important;
  padding: 0.2rem 0.5rem!important;
  border-radius: 0.5rem!important;
  height: 2rem!important;
  width: 2rem!important;
}

@keyframes slidedown {
  from {
      height: 0;
      opacity: 0;
  }
  to {
      height: auto;
      opacity: 1;
  }
}

@keyframes slideup {
  from {
      height: auto;
      opacity: 1;
  }
  to {
      height: 0;
      opacity: 0
  }
}